/* Small Devices, Tablets */
@media only screen and (max-width : 768px) {
	/**/
	html {
		font-size: 7px; } }


/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px) {
	/**/
	html {
		font-size: 5px; }
	.main .section-mask svg .title {
		font-size: 135vw; }
	.header-menu {
		&__row {
			&--logo {
				&__link {
					& svg {
						width: 15.7rem;
						height: 5.2rem; } } } } }
	.aside {
		&__link {
			&.mob {
				& svg path {
					fill: #333; } } }
		&.mes {
			bottom: 9.5rem;
			& svg {
				width: 3.2rem;
				height: 2.3rem; } }
		&.in {
			& svg {
				width: 2.8rem;
				height: 2.6rem; } } }

	.main {
		& .section {
			&-content {
				&--contact {
					width: 100%; } } } }

	.header-menu__row {
		&--menu__link {
			width: 4.2rem;
			height: 2.8rem;
			& span {
				display: block;
				position: absolute;
				height: .3rem;
				width: 100%;
				background: #fff;
				opacity: 1;
				right: 0;
				transform: rotate(0deg);
				transition: .2s ease-in-out;
				&.mob {
					background: #333; }
				&:nth-child(1) {
					top: 0;
					transform-origin: left center; }
				&:nth-child(2) {
					top: 1.25rem;
					width: 60%;
					transform-origin: left center; }
				&:nth-child(3) {
					top: 2.5rem;
					width: 80%;
					transform-origin: left center; } }
			&:hover {
				& span {
					width: 100%; } }
			&.open {
				& span {
					&:nth-child(1) {
						transform: rotate(45deg);
						top: -.7rem;
						right: -.8rem; }
					&:nth-child(2) {
						width: 0%;
						opacity: 0; }
					&:nth-child(3) {
						transform: rotate(-45deg);
						top: 2.2rem;
						right: -.8rem;
						width: 100%; } } } }
		& .list {
			&-menu {
				& li {
					margin: 0 1.5rem; } }
			&-link {
				&.mob {
					color: #333; } } } }
	#fp-nav {
		top: 43%;
		& ul {
			& li {
				width: 1.8rem;
				height: 1.8rem;
				margin: 5rem 0 0 0;
				&:hover {
					& a {
						& span {
							width: 1.8rem;
							height: 1.8rem; }
						&.active {
							& span {
								width: 1.8rem;
								height: 1.8rem;
								&.mob {
									background: #333; } } } } }

				& a {
					& span {
						width: 1.8rem;
						height: 1.8rem;
						&.mob {
							border-color: #333; } }
					&.active {
						& span {
							width: 1.8rem;
							height: 1.8rem;
							&.mob {
								background: #333; } } } } } } } }



